import {
    mapState,
    mapMutations
} from 'vuex';
import weCropper from '../../../../api/weCropper';
import store from "@/store/index.js";
import api from "@/api/api.js";
const device = uni.getSystemInfoSync();
const width = device.windowWidth;
const height = device.windowHeight - 50;
console.log(device);
export default {
    computed: mapState(['hasLogin', 'userInfo', 'token']),
    data() {
        return {
            cropperOpt: {
                id: 'cropper',
                width: width,
                height: height,
                scale: 2.5,
                zoom: 8,
                cut: {
                    x: (width - 295) / 2,
                    y: (height - 295) / 2,
                    width: 295,
                    height: 295
                }
            },
            weCropper: ''
        };
    },
    methods: {
        back() {
            uni.redirectTo({
                url: 'change-userInfo'
            });
        },
        touchStart(e) {
            this.weCropper.touchStart(e);
        },
        touchMove(e) {
            this.weCropper.touchMove(e);
        },
        touchEnd(e) {
            this.weCropper.touchEnd(e);
        },
        convertBase64UrlToBlob(dataURI, type) {
            var binary = atob(dataURI.split(',')[1]);
            var array = [];
            for (var i = 0; i < binary.length; i++) {
                array.push(binary.charCodeAt(i));
            }
            return new Blob([new Uint8Array(array)], {
                type: type
            }, {
                filename: '1111.jpg'
            });
        },
        blobToDataURL(blob) {
            var a = new FileReader();
            a.readAsDataURL(blob); //读取文件保存在result中
            a.onload = function(e) {
                var getRes = e.target.result; //读取的结果在result中
                console.log(getRes);
            };
        },
        getCropperImage() {
            let _this = this;
            let pathurl = api.urls.postHead;
            // 上传服务器地址
            this.weCropper.getCropperImage(avatar => {
                if (avatar) {
                    //  获取到裁剪后的图片
                    //  获取到裁剪后的图片
                    console.log(avatar);
                    // this.blobToDataURL(avatar);
                    wx.redirectTo({
                        url: 'change-userInfo?avatar=' + avatar
                    })
                    var that = this
                    //下面是上传到服务器的方法
                    uni.uploadFile({
                        url: pathurl,
                        filePath: avatar,
                        name: 'image',
                        header: {
                            token: that.token
                        },
                        success: res => {
                            console.log('uploadImage success, res is:', res);
                            uni.showToast({
                                title: '上传成功',
                                icon: 'success',
                                duration: 1000
                            });
                        },
                        fail: err => {
                            console.log('uploadImage fail', err);
                            uni.showModal({
                                content: err.errMsg,
                                showCancel: false
                            });
                            uni.hideLoading();
                        },
                        complete: () => {
                            console.log('complate');
                        }
                    });
                } else {
                    console.log('获取图片失败，请稍后重试');
                }
            });
        },
        uploadTap() {
            const self = this;

            uni.chooseImage({
                count: 1, // 默认9
                sizeType: ['compressed'], // 可以指定是原图还是压缩图，默认二者都有
                sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
                success(res) {
                    let src = res.tempFilePaths[0];
                    //  获取裁剪图片资源后，给data添加src属性及其值

                    self.weCropper.pushOrign(src);
                }
            });
        }
    },
    onLoad(option) {
        console.log(width, height, device);
        // do something
        const cropperOpt = this.cropperOpt;
        const src = option.src;
        if (src) {
            Object.assign(cropperOpt, {
                src
            });
            console.log(src);
            this.weCropper = new weCropper(cropperOpt)
                .on('ready', function(ctx) {
                    console.log("reday");
                })
                .on('beforeImageLoad', ctx => {
                    uni.showToast({
                        title: '上传中',
                        icon: 'loading',
                        duration: 3000
                    });
                    console.log("beforeImageLoad");
                })
                .on('imageLoad', ctx => {
                    uni.hideToast();
                    console.log("imageLoad");
                });
        }
    }
};
